.channel-settings, .channel-settings-green {
    margin-top: 15px;
    padding-top: 15px;
    padding-bottom: 15px;
    background-color: #d4eff4;
    /*border-radius: 7px;*/
    /*height: 100px;*/
}

.channel-settings .title {
    margin-bottom: 15px;
}

.channel-settings .input-div input {
    width: 100%;
    padding-right: 25px;
    margin-bottom: 10px;
}

.channel-settings .fa-info-circle {
    margin-left: 6px;
    font-size: 16px;
}

.channel-settings .fa-pencil-square {
    margin-right: 10px;
    font-size: 20px;
    position: relative;
    top: 2px;
}

.channel-settings .none, .channel-settings .none:hover {
    color: transparent;
}

.channel-settings-check-box {
    position: absolute;
    top: 30px;
    left: 25px;
}

.channel-settings .info-dropdown {
    width: 100%;
    position: absolute;
    top: 5px;
    left: 0px;
}

.channel-settings .info-dropdown .dropdown-menu {
    margin-left: 40px;
    position: absolute;
    top: 120px;
    right: 40px;
}

.channel-settings .info-circle {
    display: block;
    position: absolute;
    top: 20px;
    right: 13px;
}

.channel-settings .info-error-dropdown {
    display: block;
}

.channel-settings .info-error-dropdown .dropdown-menu{
    background-color: #faa;
    width: 100%;
    position: absolute;
    top: 75px;
    right: 0px;
    padding: 7px;
}

.channel-settings .info-error {
    font-size: 20px;
    position: absolute;
    right: -2px;
    top: 2px;
    color: #a00;
}

.channel-settings .info-error:hover {
    color: #d00;
}

