.frequency-conf-form {
    padding: 0;
}

.frequency-conf-form-footer {
    margin-top: 15px;
    padding: 15px;
    background-color: #f1d39a;
    border-radius: 7px;
}

.frequency-conf-form .additional-channels-spoiler-button{
    border-radius: 10px;
    margin: 15px 0 0px 7px;
}

.frequency-conf-form .additional-channels-spoiler-button .fa{
    margin-left: 10px;
}

/*info button*/
.frequency-conf-form .fa-info-circle {
    color: #7b8ff8;
}

.frequency-conf-form .fa-info-circle:hover {
    color: #41d5cb;
}

/*radio svitch*/
.frequency-conf-form .radio-switch-outer-circle {
    border-color: #7b8ff8;/*647dfd*/

}

.frequency-conf-form .radio-switch-inner-circle {
    background-color: #7b8ff8;
}
