.error-indicator-container {
    padding-bottom: 15px;
}

.error-indicator {
    padding: 5px;
    border: solid 2px #cc4545;
    background-color: #f19a9a;
    border-radius: 10px;
    text-align: center;
    clear: both;
}

.error-indicator .fa-exclamation-triangle{
    font-size: 70px;
    color: #cc4545;
}
