.localization-toggle {
    height: 20px;
    font-size: 15px;
}

.localization-toggle button {
    background-color: transparent;
    color: #466df7;
    padding: 0;
    margin-right: 5px;
    border: none;
    height: 20px;
    font-size: 15px;
    border-bottom: 1px solid transparent;
}

.localization-toggle button:hover {
    color: #1a45dd;
}

.localization-toggle .checked {
    color: #1a45dd;
    cursor: default;
    border-bottom: 1px solid #1a45dd;
}

.localization-toggle .checked:hover {
    color: #466df7;
}
