.small-vertical-dots {
    display: inline-block;
    width: 16px;
    height: 16px;
    position: absolute;
    top: 13px;
    right: 9px;
    border-radius: 2px;
    padding-left: 1px;
    padding-top: 1px;
    background-color: #4b68f9;
}

.small-vertical-dots div {
    margin: 2px;
    width: 10px;
    height: 2px;
    border-radius: 1px;
    background-color: #fff;
}

.small-vertical-dots div:nth-child(1) {
    animation: small-vertical-dots-1 1.5s linear infinite;
}

.small-vertical-dots div:nth-child(2) {
    animation: small-vertical-dots-2 1.5s linear infinite;
}

.small-vertical-dots div:nth-child(3) {
    animation: small-vertical-dots-3 1.5s linear infinite;
}

@keyframes small-vertical-dots-1 {
    0% {
        background-color: #8798f5;
    }
    33% {
        background-color: #fff;
    }
    66% {
        background-color: #fff;
    }
    100% {
        background-color: #8798f5;
    }
}
@keyframes small-vertical-dots-2 {
    0% {
        background-color: #fff;
    }
    33% {
        background-color: #8798f5;
    }
    66% {
        background-color: #fff;
    }
    100% {
background-color: #fff;
    }
}
@keyframes small-vertical-dots-3 {
    0% {
background-color: #fff;
    }
    33% {
        background-color: #fff;
    }
    66% {
        background-color: #8798f5;
    }
    100% {
background-color: #fff;
    }
}
