.blocking-curtain {
    position: fixed;
    width: 100%;
    height: 100%;
    color: white;
    z-index: 999;
}

.blocking-curtain div{
    min-height: 1px;
}

.blocking-curtain-background {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: black;
    z-index: 998;
    opacity: 0.75;
}

.blocking-curtain-inner {
    margin: 200px auto;
}
