.droping-list {
    margin-top: 15px;
    padding: 5px 15px;
    background-color: #ebf2f4;
    /*border-radius: 7px;*/
    height: 80px;
}

.droping-list .input-div{
    padding-right: 27px;
}

.droping-list .dropdown button {
    margin: 0;
    padding: 0;
}

.droping-list button {
    color: #466df7;
}

.droping-list button:hover {
    color: #02d0c0;
}

.droping-list .fa-info-circle {
    margin-left: 6px;
    font-size: 16px;
}

.droping-list .title{
    height: 20px;
    margin-bottom: 5px;
    padding: 0;
}

.droping-list .info-circle {
    position: absolute;
    right: -20px;
    top: -1px;
}


.droping-list select{
    width: 100%;
    background-color: white;
    border: 1px solid #aaa;
    height: 30px;
	border-radius: 4px;
	margin: 5px 0px 0 0;
    padding: 0 4px;
}
