.page-frame {
	padding: 0;
	margin: 15px;
	border-left: 1px solid #d7d7d7;
	border-right: 1px solid #d7d7d7;
}

.page-frame-title{
	padding: 10px 0;
	height: 40px;
	background-color: #7b8ff8;
	color: white;
}

.page-frame-content {
	padding: 15px;
}


/**
 *	List
 */
.page-frame .page-list .view-group {
	border-bottom: 1px solid #02d0c0;
	padding: 0 0 15px 0;
	margin: 0px 0px 15px 0px;
}

.page-frame .page-list .ender {
	border: none;
}

.page-frame .page-list .item{
    margin-bottom: 5px;
}

.page-frame .page-list .group-title {
	margin-top: 5px;
	margin-bottom: 10px;
    font-size: 20px;
}

.page-frame .page-list .item-value {
    padding-left: 30px;
}


/**
 *	Form
 */
.page-frame .page-form {
	margin: -15px -7px 0 -7px;
}

.page-frame .page-form .form-panel {
	padding: 0px 7px;
}

/**
 *  Buttons
 */
.page-frame .bottom-buttons {
	margin: 0 -10px;
}

.page-frame .bottom-buttons .container-fluid{
    padding: 0;
}

.page-frame .bottom-buttons li {
    padding: 15px 10px 0 10px;
}

.page-frame .bottom-buttons ul {
	margin: 0;
    padding: 0;
}

.page-frame .bottom-buttons button{
    border-radius: 10px;
    width: 100%;
}

.page-frame .bottom-buttons .navbar {
    margin: 0;
}
